<template>
<div class="vertical-center">
  <div class="inner-block">
    <div class="vue-tempalte">
        <form>
            <h3>Login</h3>

            <h6>{{ error }}</h6>

            <div class="form-group">
                <label>Phone</label>
                <input type="text" v-model="phone" class="form-control form-control-lg" maxlength="15" pattern="[0-9]{4} [0-9]{3} [0-9]{3}"/>
            </div>

            <div class="form-group">
                <label>Password</label>
                <input type="password" v-model="password" class="form-control form-control-lg" />
            </div>

            <button type="button" class="btn btn-dark btn-lg btn-block" v-on:click="login" :disabled="loading">Login</button>

            <p class="forgot-password text-right mt-2 mb-4">
                <router-link to="/forgot-password">Forgot password ?</router-link>
            </p>

            <div class="social-icons">
                <ul>
                    <li><a href="#"><i class="fa fa-google"></i></a></li>
                    <li><a href="#"><i class="fa fa-facebook"></i></a></li>
                    <li><a href="#"><i class="fa fa-twitter"></i></a></li>
                </ul>
            </div>

        </form>
    </div>
  </div>
</div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        data() {
            return {
                phone: '',
                password: ''
            }
        },

        computed: {
            ...mapState({
                error: state => state.auth.error,
                loading: state => state.auth.loading
            })
        },
        methods: {
            login () {
               this.$store.dispatch('auth/clearError'); 
               this.$store.dispatch('auth/setLoading'); 
               this.$store.dispatch('auth/goLogin', {phone: this.phone, password: this.password})
               .then((res) => {
                   if(res) {
                       this.$router.push("/home").catch(()=>{})
                   }
               })
            }
        },

        created() {
            this.$store.dispatch("auth/clearError")
        }
    }
</script>

<style scoped>
    h6 {
        color: #ff0000;
    }
    form{
        background: #28305f;
         color: #ffffff;
    }
</style>
